import styled from "styled-components";

const Wrapper = styled.div`
  display: flex;
  min-height: 90vh;
`;

const ContainerDesc = styled.div`
  display: flex;
  flex-direction: row;
  background-color: rgb(3, 129, 130);
  min-width: 100vw; 
`;

const TxtWrapper = styled.div`
  padding-left: 70px;
  padding-top: 100px;
`;

const Text = styled.h1`
  text-transform: Uppercase;
  display: inline-block;
  text-align: center;
  font-weight: 800;
  font-size: 56px;
  line-height: 0.95;
  color: rgb(229, 192, 87);
  -webkit-text-stroke-width: 3px;
  -webkit-text-stroke-color: black;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue";
`;

const Link = styled.a`
  -webkit-text-stroke-width: 3px;
  -webkit-text-stroke-color: black;
  text-decoration: underline;

  &:hover {
    cursor: pointer;
    background-color: #5736a3;
    color: white;
  }
`;

const Gif = styled.div`
  display: block;
  transform: scaleX(-1);
  width: 480px;
  min-height: 20%;
  background-image: url("https://media.giphy.com/media/QoncgdHL9TDm0ePySn/giphy.gif");
  background-size: contain;
  background-repeat: no-repeat;
  background-attachment: fixed;
`;

const Home = () => {
  return (
    <Wrapper>
      <ContainerDesc>
        <Gif></Gif>
        <TxtWrapper>
          <Text>
            Hey Hey Hey!
            <br />
            <br />
            HOW ABOUT
            <br /> SOME NEW
            <br /> FOLLOWINGS ??
            <br />
            <br /> JUST GO GRAB SOME{" "}
            <Link href="https://melnyky.github.io/tweets">here</Link>
          </Text>
        </TxtWrapper>
      </ContainerDesc>
    </Wrapper>
  );
};

export default Home;
